import React from "react";
import style from './Contacts.module.scss';
import {Title} from "../common/components/title/Title";
import {useForm} from "react-hook-form";
import axios from "axios";
import {toast} from "react-toastify";
// import {Zoom} from 'react-awesome-reveal';

export const Contacts = () => {
    const {
        register,
        handleSubmit,
        formState: { errors },
        reset,
    } = useForm({ mode: "onSubmit" });

    const onSubmit = (data) => {
        axios
            .post("https://node-js-gmail.vercel.app/sendMessage", data)
            .then(() => {
                toast.success("Thank you for your letter!");
            })
            .catch(() => {
                toast.error("Sorry, there's been an error");
            });
        reset();
    };

    return (
        <div id='contacts' className={style.contactsBlock}>
            <div className={style.contactsContainer}>
                {/*<Zoom>*/}
                <span>Contact</span>
                <Title text={'Contact With Me'}/>
               <form className={style.contactsForm} onSubmit={handleSubmit(onSubmit)}>
                    <div className={style.blockInfo}>
                        <label>Your name</label>
                        <input
                            {...register("name", {
                                required: "Name is required field",
                                pattern: {
                                    min: 1
                                }
                            })}
                            className={style.formArea}
                        />
                        {errors.name && <div className={style.errors}>{errors.name.message}</div>}
                    </div>

                    <div className={style.blockInfo}>
                        <label>Phone number</label>
                        <input
                            {...register("phone", {
                                required: "Phone is required field",
                            })}
                            className={style.formArea}
                        />
                        {errors.phone && <div className={style.errors}>{errors.phone.message}</div>}
                    </div>

                    <div className={style.blockInfo}>
                        <label>Email</label>
                        <input
                            {...register("email", {
                                required: "Email is required field!",
                                pattern: {
                                    value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                                    message: "Invalid email address",
                                },
                            })}
                            className={style.formArea}
                        />
                        {errors.email && <div className={style.errors}>{errors.email.message}</div>}
                    </div>

                    <div className={style.blockInfo}>
                    <label>Your message</label>
                    <textarea
                        {...register("message", {
                            required: "Message is require field",
                        })}
                        className={style.messageArea}
                    />
                    {errors.message && <div className={style.errors}>{errors.message.message}</div>}
                    </div>

                    <div className={style.blockInfo}>
                    <button type={"submit"} className={style.button}>
                        Send message
                    </button>
                    </div>
                </form>
                {/*</Zoom>*/}
            </div>
        </div>
    );
}

